<template>
  <div id="history">
    <head-top title="历史课程"></head-top>
    <div class="main">
      <!-- 课程信息 -->
      <div class="item">
        <div class="tt">课程信息</div>
        <div class="content">
          <div class="name">课程名称：{{ courseInfo["CHName"] }}</div>
          <div class="name">科目：{{ courseInfo["CourseName"] }}</div>
          <div class="info">
            <span class="tc">老师：{{ courseInfo["TeacherName"] }}</span>
            <span class="gd">班级：{{ courseInfo["ClassName"] }}</span>
          </div>
          <div class="info">
